<template>
    <div>
        <h1>SRTP </h1>
        <Timeline>
            <TimelineItem>
                <p class="time"></p>
                <div class="content">
                    <p>
                        <b>SRTP </b>{{srtp.title}}</p>
                    <p>
                        <b></b>{{srtp.description}}</p>
                    <p>
                        <b></b>{{srtp.year}}</p>
                    <p>
                        <b></b>{{srtpLevel}}</p>
                    <p>
                        <b></b>{{srtp.leader}}</p>
                    <p>
                        <b></b>{{srtp.members}}</p>
                    <p>
                        <b></b>{{srtp.teacher}}</p>
                    <p>
                        <b></b>
                        <Tag color="green">{{srtpStatus}}</Tag>
                    </p>
                </div>
            </TimelineItem>
            <TimelineItem>
                <p class="time"></p>
                <div class="content">
                    <p v-if="srtp.apply_file!=''">
                        <DownloadFile :fileId="srtp.apply_file"></DownloadFile>
                        <!-- <a href="">{{srtp.apply_file}}</a> -->
                    </p>
                    <Form v-if="srtp.status==1">
                        <UploadFile buttonTitle="" v-model="updateFile"></UploadFile>
                        <Button type="primary" @click="submitUpdate(1)" v-if="updateFile!=''"></Button>
                    </Form>
                </div>
            </TimelineItem>
            <TimelineItem>
                <p class="time"></p>
                <div class="content">
                    <p v-if="srtp.middle_file!=''">
                        <DownloadFile :fileId="srtp.middle_file"></DownloadFile>
                        <!-- <a href="">{{srtp.middle_file}}</a> -->
                    </p>
                    <Form v-if="srtp.status==2">
                        <FormItem>
                            <RadioGroup v-model="opMiddle">
                                <Radio label="101"></Radio>
                                <Radio label="102"></Radio>
                            </RadioGroup>
                        </FormItem>
                        <FormItem>
                            <UploadFile buttonTitle="" v-model="middleFile"></UploadFile>
                        </FormItem>
                        <FormItem>
                            <Button type="primary" @click="submitMiddle"></Button>
                        </FormItem>
                    </Form>
                    <Form v-if="srtp.status==3">
                        <UploadFile buttonTitle="" v-model="updateFile"></UploadFile>
                        <Button type="primary" @click="submitUpdate(2)" v-if="updateFile!=''"></Button>
                    </Form>
                </div>
            </TimelineItem>
            <TimelineItem>
                <p class="time"></p>
                <div class="content">
                    <p v-if="srtp.end_file!=''">
                        <DownloadFile :fileId="srtp.end_file"></DownloadFile>
                        <!-- <a href="">{{srtp.end_file}}</a> -->
                    </p>
                    <Form v-if="srtp.status==4||srtp.status==5">
                        <FormItem>
                            <RadioGroup v-model="opEnd">
                                <Radio label="104"></Radio>
                                <Radio label="103" v-if="srtp.status!=5"></Radio>
                                <Radio label="102"></Radio>
                            </RadioGroup>
                        </FormItem>
                        <FormItem>
                            <UploadFile buttonTitle="" v-model="endFile"></UploadFile>
                        </FormItem>
                        <FormItem>
                            <Button type="primary" @click="submitEnd"></Button>
                        </FormItem>
                    </Form>
                    <Form v-if="srtp.status==6">
                        <UploadFile buttonTitle="" v-model="updateFile"></UploadFile>
                        <Button type="primary" @click="submitUpdate(3)"></Button>
                    </Form>
                </div>
            </TimelineItem>
        </Timeline>
    </div>
</template>

<script>
import UploadFile from "../UploadFile.vue";
import DownloadFile from "../DownloadFile.vue";

export default {
    components: {
        UploadFile,
        DownloadFile
    },
    data() {
        return {
            srtp: {
                title: "",
                description: "",
                year: "",
                level: 1,
                status: "1"
            },
            updateFile: "",
            opMiddle: "101",
            middleFile: "",
            opEnd: "104",
            endFile: ""
        };
    },
    methods: {
        fetchData() {
            this.$axios
                .get("/api/student/srtp")
                .then(response => {
                    this.srtp = response.data;
                })
                .catch(error => {
                    console.error(error);
                });
        },
        submitUpdate(fileType) {
            //1- 2- 3-
            if (this.updateFile == "") {
                this.$Message.error("");
                return false;
            }
            this.$axios
                .patch("/api/student/srtp/update-file", {
                    type: fileType,
                    file: this.updateFile
                })
                .then(response => {
                    if (response.data.status == "OK")
                        this.$Message.success("");
                    else this.$Message.error("");
                    this.fetchData();
                })
                .catch(error => {
                    this.$Message.error("");
                    this.fetchData();
                });
        },
        submitMiddle() {
            var valid = this.middleFile != "" && this.opMiddle != "";
            if (!valid) this.$Message.error("");
            else {
                this.$axios
                    .patch("/api/student/srtp/update", {
                        operation: parseInt(this.opMiddle),
                        middle_file: this.middleFile
                    })
                    .then(response => {
                        if (response.data.status == "OK")
                            this.$Message.success("");
                        else this.$Message.error("");
                        this.fetchData();
                    })
                    .catch(error => {
                        this.$Message.error("");
                    });
            }
        },
        submitEnd() {
            var valid = this.endFile != "" && this.opEnd != "";
            if (!valid) this.$Message.error("");
            else {
                this.$axios
                    .patch("/api/student/srtp/update", {
                        operation: parseInt(this.opEnd),
                        end_file: this.endFile
                    })
                    .then(response => {
                        if (response.data.status == "OK")
                            this.$Message.success("");
                        else this.$Message.error("");
                        this.fetchData();
                    })
                    .catch(error => {
                        this.$Message.error("");
                    });
            }
        }
    },
    computed: {
        srtpLevel() {
            if (this.srtp.level == 1) return "";
            else if (this.srtp.level == 2) return "";
            else return "";
        },
        srtpStatus() {
            var statusName = "";
            switch (this.srtp.status) {
                case 1:
                    statusName = "";
                    break;
                case 2:
                    statusName = "";
                    break;
                case 3:
                    statusName = "";
                    break;
                case 4:
                    statusName = "";
                    break;
                case 5:
                    statusName = "";
                    break;
                case 6:
                    statusName = "";
                    break;
                case 7:
                    statusName = "";
                    break;
                case 8:
                    statusName = "";
                    break;
                default:
                    break;
            }
            return statusName;
        }
    },
    beforeMount() {
        this.fetchData();
    }
};
</script>

<style>
.time {
    font-size: 16px;
    font-weight: bold;
}
.content {
    padding-left: 5px;
    padding-top: 10px;
}
</style>
