<template>
    <div>
        <Row>
            <b>:</b>
            <span class="expand-value">{{ row.description }}</span>
        </Row>
        <Row>
            <div v-if="row.status==1">
                <h4></h4>
                <p>
                    <DownloadFile :fileId="row.apply_file"></DownloadFile>
                    <!-- <a href="">{{row.apply_file}}</a> -->
                </p>
                <Form>
                    <FormItem label="">
                        <RadioGroup v-model="updateTeachLevel">
                            <Radio label="1"></Radio>
                            <Radio label="2"></Radio>
                            <Radio label="3"></Radio>
                        </RadioGroup>
                    </FormItem>
                    <Button type="primary" size="small" @click="processRequest(201)"></Button>
                    <Button type="error" size="small" @click="processRequest(204)"></Button>
                </Form>
            </div>

            <div v-if="row.status==2">
                <h4></h4>
                <p>
                    <DownloadFile :fileId="row.apply_file"></DownloadFile>
                    <!-- <a href="">{{row.apply_file}}</a> -->
                </p>
            </div>

            <div v-if="row.status==3">
                <h4></h4>
                <p>
                    <DownloadFile :fileId="row.apply_file"></DownloadFile>
                    <!-- <a href="">{{row.apply_file}}</a> -->
                </p>
                <p>
                    <DownloadFile :fileId="row.middle_file"></DownloadFile>
                    <!-- <a href="">{{row.middle_file}}</a> -->
                </p>
                <Button type="primary" size="small" @click="processRequest(202)"></Button>
                <Button type="error" size="small" @click="processRequest(204)"></Button>
            </div>

            <div v-if="row.status==4">
                <h4></h4>
                <p>
                    <DownloadFile :fileId="row.apply_file"></DownloadFile>
                    <!-- <a href="">{{row.apply_file}}</a> -->
                </p>
                <p>
                    <DownloadFile :fileId="row.middle_file"></DownloadFile>
                    <!-- <a href="">{{row.middle_file}}</a> -->
                </p>
            </div>

            <div v-if="row.status==5">
                <h4></h4>
                <p>
                    <DownloadFile :fileId="row.apply_file"></DownloadFile>
                    <!-- <a href="">{{row.apply_file}}</a> -->
                </p>
                <p>
                    <DownloadFile :fileId="row.middle_file"></DownloadFile>
                    <!-- <a href="">{{row.middle_file}}</a> -->
                </p>
                <p>
                    <DownloadFile :fileId="row.end_file"></DownloadFile>
                    <!-- <a href="">{{row.end_file}}</a> -->
                </p>
                <Button type="primary" size="small" @click="processRequest(203)"></Button>
                <Button type="error" size="small" @click="processRequest(204)"></Button>
            </div>

            <div v-if="row.status==6">
                <h4></h4>
                <p>
                    <DownloadFile :fileId="row.apply_file"></DownloadFile>
                    <!-- <a href="">{{row.apply_file}}</a> -->
                </p>
                <p>
                    <DownloadFile :fileId="row.middle_file"></DownloadFile>
                    <!-- <a href="">{{row.middle_file}}</a> -->
                </p>
                <p>
                    <DownloadFile :fileId="row.end_file"></DownloadFile>
                    <!-- <a href="">{{row.end_file}}</a> -->
                </p>
            </div>

            <div v-if="row.status==7">
                <h4></h4>
                <p v-if="row.abort_file!=''">
                    <DownloadFile :fileId="row.abort_file"></DownloadFile>
                    <!-- <a href="">{{row.abort_file}}</a> -->
                </p>
            </div>
        </Row>
    </div>
</template>

<script>
import DownloadFile from "../DownloadFile.vue";

export default {
    components: {
        DownloadFile
    },
    props: {
        row: Object
    },
    data() {
        return {
            updateTeachLevel: this.row.level + ""
        };
    },
    methods: {
        processRequest(op) {
            this.$axios
                .patch("/api/assistant/teach/" + this.row.id + "/update", {
                    operation: op,
                    new_level: this.updateTeachLevel
                })
                .then(response => {
                    if (response.data.status == "OK")
                        this.$Message.success("");
                    else this.$Message.error("");
                    this.$emit("processed");
                })
                .catch(error => {
                    this.$Message.error("");
                });
        }
    }
};
</script>

<style scoped>
.expand-row {
    margin-bottom: 16px;
}
.ivu-btn {
    margin-right: 5px;
}
