<template>
    <div>
        <h1></h1>
        <Timeline>
            <TimelineItem>
                <p class="time"></p>
                <div class="content">
                    <p>
                        <b></b>{{teach.title}}</p>
                    <p>
                        <b></b>{{teach.description}}</p>
                    <p>
                        <b></b>{{teach.year}}</p>
                    <p>
                        <b></b>{{teachLevel}}</p>
                    <p>
                        <b></b>
                        <Tag color="green">{{teachStatus}}</Tag>
                    </p>
                </div>
            </TimelineItem>
            <TimelineItem>
                <p class="time"></p>
                <div class="content">
                    <p v-if="teach.status==1"></p>
                    <p v-if="teach.status>1"></p>
                </div>
            </TimelineItem>
            <TimelineItem>
                <p class="time"></p>
                <div class="content">
                    <p v-if="teach.middle_file!=''">
                        <DownloadFile :fileId="teach.middle_file"></DownloadFile>
                        <!-- <a href="">{{teach.middle_file}}</a> -->
                    </p>
                    <Form v-if="teach.status==2">
                        <FormItem>
                            <RadioGroup v-model="opMiddle">
                                <Radio label="101"></Radio>
                                <Radio label="103"></Radio>
                            </RadioGroup>
                        </FormItem>
                        <FormItem>
                            <UploadFile buttonTitle="" v-model="uploadedMiddleFile"></UploadFile>
                        </FormItem>
                        <FormItem>
                            <Button type="primary" @click="submitMiddle"></Button>
                        </FormItem>
                    </Form>
                </div>
            </TimelineItem>
            <TimelineItem>
                <p class="time"></p>
                <div class="content">
                    <p v-if="teach.end_file!=''">
                        <DownloadFile :fileId="teach.end_file"></DownloadFile>
                        <!-- <a href="">{{teach.end_file}}</a> -->
                    </p>
                    <Form v-if="teach.status==4">
                        <FormItem>
                            <RadioGroup v-model="opEnd">
                                <Radio label="102"></Radio>
                                <Radio label="103"></Radio>
                            </RadioGroup>
                        </FormItem>
                        <FormItem>
                            <UploadFile buttonTitle="" v-model="uploadedEndFile"></UploadFile>
                        </FormItem>
                        <FormItem>
                            <Button type="primary" @click="submitEnd"></Button>
                        </FormItem>
                    </Form>
                </div>
            </TimelineItem>
        </Timeline>
    </div>
</template>

<script>
import UploadFile from "../UploadFile.vue";
import DownloadFile from "../DownloadFile.vue";

export default {
    components: {
        UploadFile,
        DownloadFile
    },
    data() {
        return {
            teachId: this.$route.params.id,
            teach: Object,
            opMiddle: "101",
            uploadedMiddleFile: "",
            opEnd: "102",
            uploadedEndFile: ""
        };
    },
    methods: {
        fetchData() {
            this.$axios
                .get("/api/teacher/teach/" + this.teachId)
                .then(response => {
                    if (response.data.status == "OK") {
                        this.teach = response.data.teach;
                    } else this.$Message.error(response.data.msg);
                })
                .catch(error => {});
        },
        submitMiddle() {
            var valid = this.uploadedMiddleFile != "" && this.opMiddle != "";
            if (!valid) this.$Message.error("");
            else {
                this.$axios
                    .patch("/api/teacher/teach/" + this.teachId + "/update", {
                        operation: parseInt(this.opMiddle),
                        middle_file: this.uploadedMiddleFile
                    })
                    .then(response => {
                        if (response.data.status == "OK")
                            this.$Message.success("");
                        else this.$Message.error("");
                        this.fetchData();
                    })
                    .catch(error => {
                        this.$Message.error("");
                    });
            }
        },
        submitEnd() {
            var valid = this.uploadedEndFile != "" && this.opEnd != "";
            if (!valid) this.$Message.error("");
            else {
                this.$axios
                    .patch("/api/teacher/teach/" + this.teachId + "/update", {
                        operation: parseInt(this.opEnd),
                        end_file: this.uploadedEndFile
                    })
                    .then(response => {
                        if (response.data.status == "OK")
                            this.$Message.success("");
                        else this.$Message.error("");
                        this.fetchData();
                    })
                    .catch(error => {
                        this.$Message.error("");
                    });
            }
        }
    },
    computed: {
        teachLevel() {
            if (this.teach.level == 1) return "";
            else if (this.teach.level == 2) return "";
            else return "";
        },
        teachStatus() {
            var statusName = "";
            switch (this.teach.status) {
                case 1:
                    statusName = "";
                    break;
                case 2:
                    statusName = "";
                    break;
                case 3:
                    statusName = "";
                    break;
                case 4:
                    statusName = "";
                    break;
                case 5:
                    statusName = "";
                    break;
                case 6:
                    statusName = "";
                    break;
                case 7:
                    statusName = "";
                    break;
                default:
                    break;
            }
            return statusName;
        }
    },
    beforeMount() {
        this.fetchData();
    }
};
</script>

