<template>
    <div>
        <h1>SRTP </h1>
        <Form :label-width="80" v-if="!hasProject">
            <FormItem label="">
                <Input v-model="srtpTitle"></Input>
            </FormItem>
            <FormItem label="">
                <DatePicker v-model="srtpYear" type="year" placeholder=""></DatePicker>
            </FormItem>
            <!-- <FormItem label="">
                <DatePicker v-model="srtpStartTime" type="month" placeholder=""></DatePicker>
            </FormItem>
            <FormItem label="">
                <RadioGroup v-model="srtpLatestTime">
                    <Radio label="1"></Radio>
                    <Radio label="2"></Radio>
                </RadioGroup>
            </FormItem> -->
            <FormItem label="">
                <Input v-model="srtpDesc" type="textarea" :autosize="{minRows: 2,maxRows: 5}"></Input>
            </FormItem>
            <FormItem label="">
                <RadioGroup v-model="srtpLevel">
                    <Radio label="1"></Radio>
                    <Radio label="2"></Radio>
                    <Radio label="3"></Radio>
                </RadioGroup>
            </FormItem>
            <FormItem label="">
                <Select v-model="srtpTeacher" filterable>
                    <Option v-for="item in teacherList" :value="item.id" :key="item.id">{{ item.name }}</Option>
                </Select>
            </FormItem>
            <FormItem label="">
                <Input v-model="srtpMembers"></Input>
            </FormItem>
            <FormItem lable="">
                <Upload ref="upload" action="/api/upload" :on-success="finishUpload" :before-upload="beforeUpload" :on-remove="removeUpload" :headers="tokenHeader">
                    <Button type="ghost" icon="ios-cloud-upload-outline"></Button>
                </Upload>
            </FormItem>
            <FormItem>
                <Button type="primary" :loading="postLoading" @click="submitApply"></Button>
            </FormItem>
        </Form>
        <div v-else>
            <p>SRTP</p>
            <router-link to="/workspace/srtp-detail">
                <Button></Button>
            </router-link>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            srtpTitle: "",
            srtpYear: "",
            srtpStartTime: "",
            srtpLatestTime: "",
            srtpDesc: "",
            srtpLevel: "1",
            srtpTeacher: null,
            srtpMembers: "",
            srtpApplyFile: "",
            srtpApplyFileName: "",
            uploadedFiles: [],
            teacherList: [],
            postLoading: false,
            hasProject: false
        };
    },
    computed: {
        tokenHeader() {
            return {
                Authorization: "Bearer" + localStorage.token
            };
        }
    },
    methods: {
        submitApply(name) {
            var valid =
                this.srtpTitle != "" &&
                this.srtpYear != "" &&
                this.srtpDesc != "" &&
                this.srtpLevel != "" &&
                this.srtpLevel != "" &&
                this.srtpTeacher != "" &&
                this.srtpApplyFile != "";
            if (!valid) this.$Message.error("!");
            else {
                this.postLoading = true;
                this.$axios
                    .post("/api/student/srtp", {
                        title: this.srtpTitle,
                        level: this.srtpLevel,
                        year: this.srtpYear.getFullYear(),
                        teacher: this.srtpTeacher,
                        description: this.srtpDesc,
                        members: this.srtpMembers,
                        apply_file: this.srtpApplyFile
                    })
                    .then(response => {
                        this.postLoading = false;
                        if (response.data.status == "OK") {
                            //
                            this.$Message.success("");
                            this.$router.push("/workspace/srtp-detail");
                        } else {
                            this.$Message.error(response.data.msg);
                        }
                    })
                    .catch(error => {
                        this.postLoading = false;
                        this.$Message.error("");
                    });
            }
        },
        beforeUpload() {
            this.$refs.upload.clearFiles();
        },
        finishUpload(response, file) {
            this.srtpApplyFile = response.path;
            this.srtpApplyFileName = file.name;
        },
        removeUpload() {
            this.srtpApplyFile = "";
            this.srtpApplyFileName = "";
        }
    },
    beforeMount() {
        //SRTP
        this.$axios.get("/api/student/srtp").then(response => {
            if (Object.keys(response.data).length != 0) {
                this.hasProject = true;
            }
        });
        //
        this.$axios.get("/api/teachers").then(response => {
            this.teacherList = response.data;
        });
    }
};
</script>

