<template>
    <div>
        <h1></h1>
        <div v-if="!hasGraduation">
            <p></p>
            <router-link to="/workspace/graduation-apply">
                <Button></Button>
            </router-link>
        </div>
        <Timeline v-else>
            <TimelineItem>
                <p class="time"></p>
                <div class="content">
                    <p>
                        <b></b>{{graduation.title}}</p>
                    <p>
                        <b></b>{{graduation.description}}</p>
                    <p>
                        <b></b>{{graduation.year}}</p>
                    <p>
                        <b></b>{{graduation.teacher}}</p>
                    <p>
                        <b></b>
                        <Tag color="green">{{graduationStatus}}</Tag>
                    </p>
                </div>
            </TimelineItem>
            <TimelineItem>
                <p class="time"></p>
                <div class="content">
                    <p v-if="graduation.status>1&&graduation.task_file!=''">
                        <DownloadFile :fileId="graduation.task_file"></DownloadFile>
                        <!-- <a href="">{{graduation.task_file}}</a> -->
                    </p>
                    <p v-else></p>
                </div>
            </TimelineItem>
            <TimelineItem>
                <p class="time"></p>
                <div class="content">
                    <p v-if="graduation.start_file!=''">
                        <DownloadFile :fileId="graduation.start_file"></DownloadFile>
                        <!-- <a href="">{{graduation.start_file}}</a> -->
                    </p>
                    <Form v-if="graduation.status==2">
                        <FormItem>
                            <UploadFile buttonTitle="" v-model="startFile"></UploadFile>
                        </FormItem>
                        <FormItem>
                            <Button type="primary" @click="submitStart"></Button>
                        </FormItem>
                    </Form>

                </div>
            </TimelineItem>
            <TimelineItem>
                <p class="time"></p>
                <div class="content">
                    <p v-if="graduation.status>3&graduation.middle_file!=''">
                        <DownloadFile :fileId="graduation.start_file"></DownloadFile>
                        <!-- <a href="">{{graduation.start_file}}</a> -->
                    </p>
                    <Form v-if="graduation.status==3">
                        <FormItem>
                            <UploadFile buttonTitle="" v-model="middleFile"></UploadFile>
                        </FormItem>
                        <FormItem>
                            <Button type="primary" @click="submitMiddle"></Button>
                        </FormItem>
                    </Form>
                </div>
            </TimelineItem>
            <TimelineItem>
                <p class="time"></p>
                <div class="content">
                    <p v-if="graduation.status==5&&graduation.end_file!=''">
                        <DownloadFile :fileId="graduation.end_file"></DownloadFile>
                        <!-- <a href="">{{graduation.end_file}}</a> -->
                    </p>
                    <Form v-if="graduation.status==4">
                        <FormItem>
                            <UploadFile buttonTitle="" v-model="endFile"></UploadFile>
                        </FormItem>
                        <FormItem>
                            <Button type="primary" @click="submitEnd"></Button>
                        </FormItem>
                    </Form>
                </div>
            </TimelineItem>
        </Timeline>
    </div>
</template>

<script>
import UploadFile from "../UploadFile.vue";
import DownloadFile from "../DownloadFile.vue";
export default {
    components: {
        UploadFile,
        DownloadFile
    },
    data() {
        return {
            graduation: {
                title: "",
                description: "",
                year: "",
                status: 1
            },
            startFile: "",
            middleFile: "",
            endFile: "",
            hasGraduation: false
        };
    },
    methods: {
        fetchData() {
            this.$axios
                .get("/api/student/graduation")
                .then(response => {
                    if (Object.keys(response.data).length != 0) {
                        this.hasGraduation = true;
                    }
                    this.graduation = response.data;
                })
                .catch(error => {
                    console.error(error);
                });
        },
        submitStart() {
            if (this.startFile == "") this.$Message.error("");
            else {
                this.$axios
                    .patch("/api/student/graduation/update", {
                        operation: 101,
                        start_file: this.startFile
                    })
                    .then(response => {
                        if (response.data.status == "OK")
                            this.$Message.success("");
                        else this.$Message.error("");
                        this.fetchData();
                    })
                    .catch(error => {
                        this.$Message.error("");
                    });
            }
        },
        submitMiddle() {
            if (this.middleFile == "") this.$Message.error("");
            else {
                this.$axios
                    .patch("/api/student/graduation/update", {
                        operation: 102,
                        middle_file: this.middleFile
                    })
                    .then(response => {
                        if (response.data.status == "OK")
                            this.$Message.success("");
                        else this.$Message.error("");
                        this.fetchData();
                    })
                    .catch(error => {
                        this.$Message.error("");
                    });
            }
        },
        submitEnd() {
            if (this.endFile == "") this.$Message.error("");
            else {
                this.$axios
                    .patch("/api/student/graduation/update", {
                        operation: 103,
                        end_file: this.endFile
                    })
                    .then(response => {
                        if (response.data.status == "OK")
                            this.$Message.success("");
                        else this.$Message.error("");
                        this.fetchData();
                    })
                    .catch(error => {
                        this.$Message.error("");
                    });
            }
        }
    },
    computed: {
        graduationStatus() {
            var statusName = "";
            switch (this.graduation.status) {
                case 1:
                    statusName = "";
                    break;
                case 2:
                    statusName = "";
                    break;
                case 3:
                    statusName = "";
                    break;
                case 4:
                    statusName = "";
                    break;
                case 5:
                    statusName = "";
                    break;
                default:
                    break;
            }
            return statusName;
        }
    },
    beforeMount() {
        this.fetchData();
    }
};
</script>

<style>
.time {
    font-size: 16px;
    font-weight: bold;
}
.content {
    padding-left: 5px;
    padding-top: 10px;
}
</style>
