<template>
    <div style="background:#eee;padding-top: 100px;height:100vh">
        <Card :bordered="false" class="login-box">
            <p slot="title" style="text-align:center"></p>
            <Form :label-width="80">
                <FormItem label="">
                    <Input v-model="name"></Input>
                </FormItem>
                <FormItem label="/">
                    <Input v-model="number">
                    <span slot="append">@ustb.edu.com</span>
                    </Input>
                </FormItem>
                <FormItem label="">
                    <Input type="password" v-model="password"></Input>
                </FormItem>
                <FormItem label="">
                    <Input type="password" v-model="confirmPassword"></Input>
                </FormItem>
                <FormItem>
                    <Select v-model="role" style="width:200px">
                        <Option v-for="item in roleList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                    </Select>
                </FormItem>
                <FormItem>
                    <Button @click="doRegister" type="primary" style="margin-right:10px"></Button>
                    <router-link to="/login">
                        <Button type="default"></Button>
                    </router-link>
                </FormItem>
            </Form>
        </Card>
    </div>
</template>

<script>
export default {
    data() {
        return {
            name: "",
            number: "",
            role: 1,
            password: "",
            confirmPassword: "",
            roleList: [
                {
                    value: 1,
                    label: ""
                },
                {
                    value: 2,
                    label: ""
                },
                {
                    value: 3,
                    label: ""
                }
            ]
        };
    },
    methods: {
        doRegister() {
            //
            if (this.name == "") {
                this.$Message.error("");
                return false;
            }
            if (this.number.length < 4) {
                this.$Message.error("/4");
                return false;
            }
            if (this.password.length < 6) {
                this.$Message.error("6");
                return false;
            }
            if (this.password != this.confirmPassword) {
                this.$Message.error("");
                return false;
            }
            //
            this.$axios
                .post("/api/auth/register", {
                    name: this.name,
                    role: this.role,
                    email: this.number + "@ustb.edu.cn",
                    password: this.password
                })
                .then(response => {
                    if (response.data.status == "OK") {
                        this.$Message.success("");
                        this.$router.push("/login");
                    } else this.$Message.error(response.data.msg);
                })
                .catch(error => {
                    this.$Message.error("");
                });
        }
    },
    beforeCreate() {
        //token
        var token = localStorage.getItem("token");
        if (token && token != "") {
            this.$axios
                .post("/api/auth/me")
                .then(response => {
                    if (response.data.name) {
                        this.$router.push("workspace");
                    }
                })
                .catch(error => {
                    localStorage.removeItem("token");
                    this.$Message.error("");
                });
        }
    }
};
</script>

