<template>
    <Table border :columns="col" :data="list"></Table>
</template>
<script>
import SrtpTableExpand from "./SrtpTableExpand.vue";
export default {
    data() {
        return {
            col: [
                {
                    type: "expand",
                    width: 50,
                    render: (h, params) => {
                        return h(SrtpTableExpand, {
                            props: {
                                row: params.row
                            },
                            on: {
                                processed: this.fetchTableData
                            }
                        });
                    }
                },
                {
                    title: "",
                    key: "title"
                },
                {
                    title: "",
                    key: "year",
                    width: 100,
                    filters: [
                        {
                            label: "2018",
                            value: 2018
                        },
                        {
                            label: "2017",
                            value: 2017
                        },
                        {
                            label: "2016",
                            value: 2016
                        },
                        {
                            label: "2015",
                            value: 0
                        }
                    ],
                    filterMultiple: false,
                    filterMethod(value, row) {
                        if (value === 0) return row.year <= 2015;
                        else return row.year === value;
                    }
                },
                {
                    title: "",
                    key: "leader"
                },
                {
                    title: "",
                    key: "level",
                    width: 100,
                    render: (h, params) => {
                        var levelName;
                        switch (params.row.level) {
                            case 1:
                                levelName = "";
                                break;
                            case 2:
                                levelName = "";
                                break;
                            case 3:
                                levelName = "";
                                break;
                            default:
                                break;
                        }
                        return h("strong", levelName);
                    },
                    filters: [
                        {
                            label: "",
                            value: 1
                        },
                        {
                            label: "",
                            value: 2
                        },
                        {
                            label: "",
                            value: 3
                        }
                    ],
                    filterMultiple: false,
                    filterMethod(value, row) {
                        return row.level === value;
                    }
                },
                {
                    title: "",
                    key: "status",
                    filters: [
                        {
                            label: "",
                            value: 1
                        },
                        {
                            label: "",
                            value: 2
                        },
                        {
                            label: "",
                            value: 3
                        },
                        {
                            label: "",
                            value: 4
                        },
                        {
                            label: "",
                            value: 5
                        },
                        {
                            label: "",
                            value: 6
                        },
                        {
                            label: "",
                            value: 7
                        },
                        {
                            label: "",
                            value: 8
                        }
                    ],
                    render: (h, params) => {
                        var statusName;
                        switch (params.row.status) {
                            case 1:
                                statusName = "";
                                break;
                            case 2:
                                statusName = "";
                                break;
                            case 3:
                                statusName = "";
                                break;
                            case 4:
                                statusName = "";
                                break;
                            case 5:
                                statusName = "";
                                break;
                            case 6:
                                statusName = "";
                                break;
                            case 7:
                                statusName = "";
                                break;
                            case 8:
                                statusName = "";
                                break;
                            default:
                                break;
                        }
                        return h("span", statusName);
                    },
                    filterMultiple: false,
                    filterMethod(value, row) {
                        return row.status === value;
                    }
                }
            ],
            list: []
        };
    },
    methods: {
        fetchTableData() {
            this.$axios.get("/api/assistant/srtp").then(response => {
                this.list = response.data;
            });
        }
    },
    beforeMount() {
        this.fetchTableData();
    }
};
</script>
