<template>
    <div>
        <h1> </h1>
        <Form>
            <FormItem label="">
                <Input v-model="formItem.teachTitle"></Input>
            </FormItem>
            <FormItem label="">
                <Input v-model="formItem.teachDesc" type="textarea" :autosize="{minRows: 2,maxRows: 5}"></Input>
            </FormItem>
            <FormItem label="">
                <DatePicker v-model="formItem.teachYear" type="year" placeholder=""></DatePicker>
            </FormItem>
            <FormItem label="">
                <RadioGroup v-model="formItem.teachLevel">
                    <Radio label="1"></Radio>
                    <Radio label="2"></Radio>
                    <Radio label="3"></Radio>
                </RadioGroup>
            </FormItem>
            <FormItem>
                <UploadFile buttonTitle="" v-model="formItem.teachApplyFile"></UploadFile>
            </FormItem>
            <Button type="primary" @click="submitApply" :loading="postLoading"></Button>
        </Form>
    </div>
</template>

<script>
import UploadFile from "../UploadFile.vue";

export default {
    components: {
        UploadFile
    },
    data() {
        return {
            formItem: {
                teachTitle: "",
                teachDesc: "",
                teachYear: "",
                teachLevel: "1",
                teachApplyFile: ""
            },
            postLoading: false
        };
    },
    methods: {
        submitApply() {
            var valid =
                this.formItem.teachTitle != "" &&
                this.formItem.teachDesc != "" &&
                this.formItem.teachYear != "" &&
                this.formItem.teachLevel != "" &&
                this.formItem.teachApplyFile != "";
            if (!valid) {
                this.$Message.error("!");
                return false;
            }
            this.$axios
                .post("/api/teacher/teach", {
                    title: this.formItem.teachTitle,
                    description: this.formItem.teachDesc,
                    level: this.formItem.teachLevel,
                    year: this.formItem.teachYear.getFullYear(),
                    apply_file: this.formItem.teachApplyFile
                })
                .then(response => {
                    this.postLoading = false;
                    if (response.data.status == "OK") {
                        //
                        this.$Message.success("");
                        this.$router.push("/workspace/my-teach-list");
                    } else {
                        this.$Message.error(response.data.msg);
                    }
                })
                .catch(error => {
                    this.postLoading = false;
                    this.$Message.error("");
                });
        }
    }
};
</script>

